.cross-button {
  @include reset-button;
  @include square(48px);

  position: relative;
  border-radius: var(--border-radius-small);

  @include hover {
    background-color: var(--color-accent);

    &::before,
    &::after {
      background-color: white; // Устанавливаем белый цвет
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 7px;
    width: 70%;
    height: 3.5px;
    background-color: var(--burger-color);
    border-radius: 5px;
  }

  &::before {
    rotate: 45deg;
  }

  &::after {
    rotate: -45deg;
  }
}
